const aboutRequested = () => {
    return {
        type: 'FETCH_ABOUT_REQUEST'
    }
};

const aboutLoaded = (newAbout) => {
    return {
        type: 'FETCH_ABOUT_SUCCESS',
        payload: newAbout
    };
};

const aboutError = (error) => {
    return {
        type: 'FETCH_ABOUT_FAILURE',
        payload: error
    };
};

const fetchAbout = (dataService) => () => (dispatch) => {
    const aboutRequest = dataService.getAbout();
    if (aboutRequest instanceof Promise) {
        dispatch(aboutRequested());
        aboutRequest
            .then((data) => {
                dispatch(aboutLoaded(data))
            })
            .catch((err) => dispatch(aboutError(err)));
    } else {
        dispatch(aboutLoaded(aboutRequest));
    }
};

export {
    fetchAbout
};
