import React from 'react';
import EventItem from '../event-item/event-item';

const EventList = (props) => {
    const { events } = props;
    return (
        <div>
            {
                events.map((event) => {
                    return (
                        <EventItem key={event.id} event={event} />
                    );
                })
            }
        </div>
    );
};

export default EventList;
