const chatsRequested = () => {
    return {
        type: 'FETCH_CHATS_REQUEST'
    }
};

const chatsLoaded = (newChats) => {
    return {
        type: 'FETCH_CHATS_SUCCESS',
        payload: newChats
    };
};

const chatsError = (error) => {
    return {
        type: 'FETCH_CHATS_FAILURE',
        payload: error
    };
};

const filterChats = (filter) => {
    return {
        type: 'FILTER_CHATS',
        payload: filter
    };
};


const fetchChats = (dataService) => () => (dispatch) => {
    const chatsRequest = dataService.getChats();
    if (chatsRequest instanceof Promise) {
        dispatch(chatsRequested());
        chatsRequest
            .then((data) => {
                dispatch(chatsLoaded(data))
            })
            .catch((err) => dispatch(chatsError(err)));
    } else {
        dispatch(chatsLoaded(chatsRequest));
    }
};

export {
    fetchChats,
    filterChats
};
