const eventValueChange = (value, target) => {
    return {
        type: 'EVENT_VALUE_CHANGE',
        value,
        target
    };
};

const postRequested = () => {
    return {
        type: 'POST_EVENT_REQUEST'
    }
};

const postLoaded = () => {
    return {
        type: 'POST_EVENT_SUCCESS',
    };
};

const postError = (error) => {
    return {
        type: 'POST_EVENT_FAILURE',
        payload: error
    };
};

const postEvent = (dataService) => (data) => (dispatch) => {
    dispatch(postRequested());
    dataService.postEvent(data)
        .then(() => dispatch(postLoaded()))
        .catch((err) => dispatch(postError(err)));
};

export {
    eventValueChange,
    postEvent
};
