const initialState = {
    sponsors: [],
    loading: true,
    error: null
}

const sponsorList = (state = initialState, action) => {

    switch (action.type) {
        case 'FETCH_SPONSORS_REQUEST':
            return {
                sponsors: [],
                loading: true,
                error: null,
            };

        case 'FETCH_SPONSORS_SUCCESS':
            return {
                sponsors: action.payload,
                loading: false,
                error: null,
            };

        case 'FETCH_SPONSORS_FAILURE':
            return {
                sponsors: [],
                loading: false,
                error: action.payload,
            };

        default:
            return state;
    }
};

export default sponsorList;
