const initialState = {
    filter: 'all',
    chats: [],
    loading: true,
    error: null
}

let allChats;
const filterChats = (state, action) => {
    if (!allChats) {
        allChats = state.chats;
    }
    if (action.payload === 'all') {
        return {...state, filter: 'marketing', chats: allChats}
    }
    const filteredChats = allChats.filter((item) => {
        return item.scope.find(it => it.toLowerCase() === action.payload)
    });
    return {...state, filter: 'marketing', chats: filteredChats}
}

const chatList = (state = initialState, action) => {

    switch (action.type) {
        case 'FETCH_CHATS_REQUEST':
            return {
                ...state,
                chats: [],
                loading: true,
                error: null,
            };

        case 'FETCH_CHATS_SUCCESS':
            return {
                ...state,
                chats: action.payload,
                loading: false,
                error: null,
            };

        case 'FETCH_CHATS_FAILURE':
            return {
                ...state,
                chats: [],
                loading: false,
                error: action.payload,
            };

        case 'FILTER_CHATS':
            return filterChats(state, action)

        default:
            return state;
    }
};

export default chatList;
