const initialState = {
    socials: [],
    loading: true,
    error: null
}

const socialList = (state = initialState, action) => {

    switch (action.type) {
        case 'FETCH_SOCIALS_REQUEST':
            return {
                socials: [],
                loading: true,
                error: null,
            };

        case 'FETCH_SOCIALS_SUCCESS':
            return {
                socials: action.payload,
                loading: false,
                error: null,
            };

        case 'FETCH_SOCIALS_FAILURE':
            return {
                socials: [],
                loading: false,
                error: action.payload,
            };

        default:
            return state;
    }
};

export default socialList;
