import formValidation from "../utils/form-validation";

const initialState = {
    successSubmit: false,
    errorSubmit: false,
    loadingSubmit: false,
    isFormValid: false,
    formControls: {
        eventName: {
            value: '',
            name: 'eventName',
            label: 'Имя',
            valid: false,
            touched: false,
            errorMessage: 'Поле не заполнено',
            validation: {
                required: true,
            }
        },
        eventEmail: {
            value: '',
            type: 'email',
            name: 'eventEmail',
            label: 'Email',
            valid: false,
            touched: false,
            validation: {
                required: true,
                email: true,
            }
        },
        eventPhone: {
            value: '',
            name: 'eventPhone',
            label: 'Телефон',
            valid: false,
            touched: false,
            errorMessage: 'Поле не заполнено',
            validation: {
                required: true,
            }
        },
        eventLink: {
            value: '',
            name: 'eventLink',
            label: 'Ссылка на мероприятие',
        }
    }
}

const updateValidation = (state, value, target) => {
    const {isFormValid, formControls} = formValidation(state, value, target);
    return {
        ...state,
        isFormValid,
        formControls
    };
}


const addEventForm = (state = initialState, action) => {
    switch (action.type) {
        case 'EVENT_VALUE_CHANGE':
            return updateValidation(state, action.value, action.target);
        case 'POST_EVENT_REQUEST':
            return {
                ...state,
                loadingSubmit: true,
                successSubmit: false,
                errorSubmit: false,
            }
        case 'POST_EVENT_FAILURE':
            return {
                ...state,
                loadingSubmit: false,
                successSubmit: false,
                errorSubmit: action.payload,
            }
        case 'POST_EVENT_SUCCESS':
            return {
                ...initialState,
                successSubmit: true,
            }
        default:
            return state;
    }
};

export default addEventForm;
