const initialState = {
    about: [],
    loading: true,
    error: null
}

const aboutInfo = (state = initialState, action) => {

    switch (action.type) {
        case 'FETCH_ABOUT_REQUEST':
            return {
                about: [],
                loading: true,
                error: null,
            };

        case 'FETCH_ABOUT_SUCCESS':
            return {
                about: action.payload,
                loading: false,
                error: null,
            };

        case 'FETCH_ABOUT_FAILURE':
            return {
                about: [],
                loading: false,
                error: action.payload,
            };

        default:
            return state;
    }
};

export default aboutInfo;
