const sponsorsRequested = () => {
    return {
        type: 'FETCH_SPONSORS_REQUEST'
    }
};

const sponsorsLoaded = (newSponsors) => {
    return {
        type: 'FETCH_SPONSORS_SUCCESS',
        payload: newSponsors
    };
};

const sponsorsError = (error) => {
    return {
        type: 'FETCH_SPONSORS_FAILURE',
        payload: error
    };
};

const fetchSponsors = (dataService) => () => (dispatch) => {
    dispatch(sponsorsRequested());
    dataService.getSponsors()
        .then((data) => dispatch(sponsorsLoaded(data)))
        .catch((err) => dispatch(sponsorsError(err)));
};

export {
    fetchSponsors
};
