const chatValueChange = (value, target) => {
    return {
        type: 'CHAT_VALUE_CHANGE',
        value,
        target
    };
};

const postRequested = () => {
    return {
        type: 'POST_CHAT_REQUEST'
    }
};

const postLoaded = () => {
    return {
        type: 'POST_CHAT_SUCCESS',
    };
};

const postError = (error) => {
    return {
        type: 'POST_CHAT_FAILURE',
        payload: error
    };
};

const postChat = (dataService) => (data) => (dispatch) => {
    dispatch(postRequested());
    dataService.postChat(data)
        .then(() => dispatch(postLoaded()))
        .catch((err) => dispatch(postError(err)));
};

export {
    chatValueChange,
    postChat
};
