const initialState = {
    events: [],
    loading: true,
    error: null
};

const filterEvents = (state, action) => {

};

const eventList = (state = initialState, action) => {

    switch (action.type) {
        case 'FETCH_EVENTS_REQUEST':
            return {
                ...state,
                events: [],
                loading: true,
                error: null
            };

        case 'FETCH_EVENTS_SUCCESS':
            return {
                ...state,
                events: action.payload,
                loading: false,
                error: null
            };

        case 'FETCH_EVENTS_FAILURE':
            return {
                ...state,
                events: [],
                loading: false,
                error: action.payload
            };

        case 'FILTER_EVENTS':
            return filterEvents(state, action)

        default:
            return state;
    }
};

export default eventList;
