const socialsRequested = () => {
    return {
        type: 'FETCH_SOCIALS_REQUEST'
    }
};

const socialsLoaded = (newSocials) => {
    return {
        type: 'FETCH_SOCIALS_SUCCESS',
        payload: newSocials
    };
};

const socialsError = (error) => {
    return {
        type: 'FETCH_SOCIALS_FAILURE',
        payload: error
    };
};


const fetchSocials = (dataService) => () => (dispatch) => {
    const socialsRequest = dataService.getSocials();
    if (socialsRequest instanceof Promise) {
        dispatch(socialsRequested());
        socialsRequest
            .then((data) => {
                dispatch(socialsLoaded(data))
            })
            .catch((err) => dispatch(socialsError(err)));
    } else {
        dispatch(socialsLoaded(socialsRequest));
    }
};

export {
    fetchSocials
};
