import React, {Fragment} from "react";
import ChatItem from "../chat-item/chat-item";

const ChatList = (props) => {
    const { chats } = props;
    return (
        <Fragment>
            {
                chats.map((chat) => {
                    return (
                        <ChatItem key={chat.id} chat={chat} />
                    );
                })
            }
        </Fragment>
    );
};

export default ChatList;
