import formValidation from "../utils/form-validation";

const initialState = {
    successSubmit: false,
    errorSubmit: false,
    loadingSubmit: false,
    isFormValid: false,
    formControls: {
        chatName: {
            value: '',
            name: 'chatName',
            label: 'Имя',
            valid: false,
            touched: false,
            errorMessage: 'Поле не заполнено',
            validation: {
                required: true,
            }
        },
        chatPhone: {
            value: '',
            name: 'chatPhone',
            label: 'Телефон',
            valid: false,
            touched: false,
            errorMessage: 'Поле не заполнено',
            validation: {
                required: true,
            }
        },
        chatLink: {
            value: '',
            name: 'chatLink',
            label: 'Ссылка на сообщество',
        }
    }
}

const updateValidation = (state, value, target) => {
    const {isFormValid, formControls} = formValidation(state, value, target);
    return {
        ...state,
        isFormValid,
        formControls
    };
}


const chatForm = (state = initialState, action) => {
    switch (action.type) {
        case 'CHAT_VALUE_CHANGE':
            return updateValidation(state, action.value, action.target);
        case 'POST_CHAT_REQUEST':
            return {
                ...state,
                loadingSubmit: true,
                successSubmit: false,
                errorSubmit: false,
            }
        case 'POST_CHAT_FAILURE':
            return {
                ...state,
                loadingSubmit: false,
                successSubmit: false,
                errorSubmit: action.payload,
            }
        case 'POST_CHAT_SUCCESS':
            return {
                ...initialState,
                successSubmit: true,
            }
        default:
            return state;
    }
};

export default chatForm;
